// 添加客户
export function addCustomer(customer) {
  return {
    ac: 'addCustomer',
    shop: customer.shop,
    name: customer.name,
    phone: customer.phone,
    region: customer.region
  }
}

// 修改客户
export function updateCustomer(customer) {
  return {
    ac: 'updateCustomer',
    code: customer.code,
    shop: customer.shop,
    name: customer.name,
    phone: customer.phone,
    region: customer.region
  }
}

// 删除客户
export function deleteCustomer(customer) {
  return {
    ac: 'deleteCustomer',
    code: customer.code,
  }
}

// 查找客户
export function findCustomer(searchText = '', searchType = '', offset = 0, limit = 30) {
  return {
    ac: 'findCustomer',
    offset: offset,
    limit: limit,
    searchText: searchText,
    searchType: searchType
  }
}
