// 添加订单
export function addOrder(orderData) {
  return {
    ac: 'addBackOrder',
    customer_code: orderData.customer_code,
    shop: orderData.shop,
    order_write_code: orderData.order_write_code,
    num: orderData.num,
    price: orderData.price,
    state: orderData.state,
    real_price: orderData.real_price,
    orderItemList: orderData.orderItemList
  }
}
// 修改订单
export function updateOrder(orderData) {
  return {
    ac: 'updateBackOrder',
    code: orderData.code,
    customer_code: orderData.customer_code,
    shop: orderData.shop,
    order_write_code: orderData.order_write_code,
    num: orderData.num,
    price: orderData.price,
    state: orderData.state,
    real_price: orderData.real_price,
    create_time: orderData.create_time,
    orderItemList: orderData.orderItemList
  }
}

// 查询订单条目
export function findOrderItem(orderData) {
  return {
    ac: 'findBackOrderItem',
    code: orderData.code,
  }
}

// 查询订单
export function findOrder(searchText = '', searchType = "-1", offset = 0, limit = 30) {
  return {
    ac: 'findBackOrder',
    offset: offset,
    limit: limit,
    searchText: searchText,
    searchType: searchType
  }
}


// 删除订单
export function deleteOrder(order) {
  return {
    ac: 'deleteBackOrder',
    code: order.code,
  }
}

// 更改销售单状态
export function stateChange(order) {
  return {
    ac: 'stateBackChange',
    code: order.code,
    state: order.state,
  }
}
