// 添加商品
export function addCommodity(commodity) {
  return {
    ac: "addCommodity",
    name: commodity.name,
    weight: commodity.weight,
    specifications: commodity.specifications,
    home: commodity.home,
    bar_code: commodity.bar_code,
    expire: commodity.expire,
    price_in: commodity.price_in,
    price_out: commodity.price_out,
    price_out_down: commodity.price_out_down,
    stock: commodity.stock,
    img_url: commodity.img_url,
  }
}

// 修改商品
export function updateCommodity(commodity) {
  return {
    ac: "updateCommodity",
    code: commodity.code,
    name: commodity.name,
    weight: commodity.weight,
    specifications: commodity.specifications,
    home: commodity.home,
    bar_code: commodity.bar_code,
    expire: commodity.expire,
    price_in: commodity.price_in,
    price_out: commodity.price_out,
    price_out_down: commodity.price_out_down,
    stock: commodity.stock,
    img_url: commodity.img_url,
  }
}

// 删除商品
export function deleteCommodity(commodity) {
  return {
    ac: 'deleteCommodity',
    code: commodity.code,
  }
}

// 查找商品
export function findCommodity(searchText = '', searchType = '', offset = 0, limit = 30) {
  return {
    ac: 'findCommodity',
    offset: offset,
    limit: limit,
    searchText: searchText,
    searchType: searchType
  }
}
