<template>
  <div>
    <Row :gutter="16" style="padding-bottom: 16px" type="flex" align="middle">
      <Col span="4">
        <Input readonly v-on:click.native="selectCustomerClick" v-model="orderData.shop">
          <span slot="prepend">客户名称</span>
        </Input>
      </Col>
      <Col span="3" style="margin-left:40px">
        <h2>总数量：{{countTotalNum}}</h2>
      </Col>
      <Col span="3">
        <h2>
          总金额：
          <span style="color:#ed4014">{{countTotalPrice}}</span>
        </h2>
      </Col>
      <Col span="1">
        <Button type="primary" @click="saveSure">保存</Button>
      </Col>
      <Col span="2">
        <span style="color:#ed4014;font-size:22px">退货单</span>
      </Col>
    </Row>

    <Table border :columns="titleList" :data="orderData.orderItemList">
      <template slot-scope="{index }" slot="name">
        <Input
          v-model="orderData.orderItemList[index].name"
          readonly
          v-on:click.native="selectCommodityClick(index)"
          type="text"
        />
      </template>
      <template
        slot-scope="{index }"
        slot="specifications"
      >1 * {{orderData.orderItemList[index].specifications}}</template>

      <template slot-scope="{index}" slot="unit">
        <Select
          v-model="orderData.orderItemList[index].unit"
          :transfer="true"
          @on-change="unitChange(orderData.orderItemList[index])"
        >
          <Option value="个" key="个">个</Option>
          <Option value="件" key="件">件</Option>
        </Select>
      </template>
      <template slot-scope="{index}" slot="num">
        <Input v-model="orderData.orderItemList[index].num" type="number" />
      </template>
      <template slot-scope="{index}" slot="price">
        <Input
          v-model="orderData.orderItemList[index].price"
          type="number"
          @on-blur="formatItemPrice(orderData.orderItemList[index])"
        />
      </template>
      <template
        slot-scope="{index}"
        slot="total_price"
      >{{countItemTotalPrice(orderData.orderItemList[index])}}</template>
      <template slot-scope="{index }" slot="action">
        <Button type="error" size="small" @click="deleteSaleModal(index)">删除</Button>
      </template>
    </Table>
    <Button style="width: 100%;" @click="addItem" size="large">添加商品</Button>

    <Modal
      title="请选择客户"
      :closable="false"
      fullscreen
      footer-hide
      closable
      v-model="selectCustomerModal"
      class-name="vertical-center-modal"
    >
      <Row :gutter="16" style="padding-bottom: 16px">
        <Col span="6">
          <Input v-model="inputCustomerText" @input="emptysDearchCustomer">
            <Select v-model="searchCustomerType" slot="prepend" style="width: 80px">
              <Option value="shop">商店名称</Option>
              <Option value="name">客户名称</Option>
              <Option value="phone">电话号码</Option>
            </Select>
            <Button slot="append" icon="ios-search" @click="findCustomer" />
          </Input>
        </Col>
      </Row>
      <Table border :columns="customerTitleList" :data="customerList">
        <template slot-scope="{row}" slot="action">
          <Button type="primary" size="small" @click="selectCustomerModalBtn(row)">选择</Button>
        </template>
      </Table>
      <Button @click="loadCustomerMore" style="width: 100%" v-if="isCustomerShowMoreBtn">加载更多</Button>
    </Modal>

    <Modal
      title="请选择商品"
      :closable="false"
      fullscreen
      footer-hide
      closable
      v-model="selectCommodityModal"
      class-name="vertical-center-modal"
    >
      <Row :gutter="16" style="padding-bottom: 16px">
        <Col span="6">
          <Input v-model="inputCommodityText" @input="emptysDearchCommodity">
            <Select v-model="searchCommodityType" slot="prepend" style="width: 80px">
              <Option value="name">商品名称</Option>
              <Option value="bar_code">条形码</Option>
            </Select>
            <Button slot="append" icon="ios-search" @click="findCommodity"></Button>
          </Input>
        </Col>
      </Row>
      <Table border :columns="commodityTitleList" :data="commodityList">
        <template slot-scope="{row}" slot="action">
          <Button type="primary" size="small" @click="selectCommodityModalBtn(row)">选择</Button>
        </template>
      </Table>
      <Button @click="loadCommodityMore" style="width: 100%" v-if="isCommodityShowMoreBtn">加载更多</Button>
    </Modal>
  </div>
</template>

<script>
import { Http_Post } from "../common/Http";
import { addOrder } from "../params/BackSaleP";

import { findCustomer } from "../params/CustomerP";
import { findCommodity } from "../params/CommodityP";
import { keepTwoDecimalFull } from "../common/Util";

import { EventBus } from "../common/EventBus";

export default {
  name: "Sale",

  data() {
    return {
      inputCustomerText: "", // 搜索框中的内容
      searchCustomerType: "shop", // 搜索类型
      customerTitleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商店名称",
          key: "shop"
        },
        {
          title: "客户名称",
          key: "name"
        },
        {
          title: "电话号码",
          key: "phone"
        },
        {
          title: "地区",
          key: "region"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // customer列表头
      customerList: [], // 列表内容customer
      isCustomerShowMoreBtn: true, // 是否显示Customer加载跟多按钮
      selectCustomerModal: false, // 是否显示选择客户的Modal

      inputCommodityText: "", // 搜索框中的内容
      searchCommodityType: "name", // 搜索类型
      commodityTitleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商品图片",
          key: "img_url"
        },
        {
          title: "商店名称",
          key: "name"
        },
        {
          title: "商品重量",
          key: "weight"
        },
        {
          title: "商品规格",
          key: "specifications"
        },
        {
          title: "出产地",
          key: "home"
        },
        {
          title: "条形码",
          key: "bar_code"
        },
        {
          title: "保质期",
          key: "expire"
        },
        {
          title: "进货价格",
          key: "price_in"
        },
        {
          title: "正发价格",
          key: "price_out"
        },
        {
          title: "批发价格",
          key: "price_out_down"
        },
        {
          title: "库存",
          key: "stock"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // commodity列表头
      commodityList: [], // 列表内容commodity
      isCommodityShowMoreBtn: true, // 是否显示Commodity加载跟多按钮
      selectCommodityModal: false, // 是否显示选择商品的Modal

      offset: 0, // 分页加载页标
      limit: 30, // 分页数据量
      readonly: true, // input只读

      titleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商品名称",
          slot: "name"
        },
        {
          title: "条形码",
          key: "bar_code"
        },
        {
          title: "规格",
          slot: "specifications"
        },
        {
          title: "单位",
          slot: "unit"
        },
        {
          title: "数量",
          slot: "num"
        },
        {
          title: "单价",
          slot: "price"
        },
        {
          title: "金额",
          slot: "total_price"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // 列表头

      itemIndex: 0, // 商品选择条目index记录

      orderData: {
        customer_code: "",
        order_write_code: "",
        num: 0,
        price: "0.00",
        shop: "",
        real_price: "0.00",
        state: 0,
        orderItemList: [] // 条目列表
      } // 提交的订单对象
    };
  },
  computed: {
    // 格式化商品金额
    formatItemPrice(row) {
      let _this = this;
      return function(row) {
        row.price = keepTwoDecimalFull(row.price);
      };
    },
    // 计算每一个条目的总金额
    countItemTotalPrice(row) {
      return function(row) {
        return keepTwoDecimalFull(row.num * row.price);
      };
    },
    // 计算总数量
    countTotalNum() {
      this.orderData.num = 0;
      this.orderData.orderItemList.map((item, index) => {
        this.orderData.num += item.num * 1;
      });
      return this.orderData.num;
    },
    // 计算总金额
    countTotalPrice() {
      var count = 0.0;
      this.orderData.orderItemList.map((item, index) => {
        count += parseFloat(item.price * item.num);
      });
      this.orderData.price = keepTwoDecimalFull(count);
      return this.orderData.price;
    }
  },
  methods: {
    // 显示选择客户的Modal
    selectCustomerClick() {
      this.offset = 0;
      this.findCustomer();
      this.selectCustomerModal = true;
    },
    // 搜索框为空，查询客户
    emptysDearchCustomer() {
      if (this.inputCustomerText === "") {
        this.offset = 0;
        this.findCustomer();
      }
    },
    // 加载跟多客户
    loadCustomerMore() {
      this.offset++;
      this.findCustomer();
    },
    // 确认选择的客户
    selectCustomerModalBtn(row) {
      this.orderData.customer_code = row.code;
      this.orderData.shop = row.shop;
      this.selectCustomerModal = false;
      this.inputCustomerText = "";
      this.searchCustomerType = "shop";
    },

    // 显示选择商品的Modal
    selectCommodityClick(index) {
      this.offset = 0;
      this.itemIndex = index;
      this.findCommodity();
      this.selectCommodityModal = true;
    },
    // 搜索框为空，查询商品
    emptysDearchCommodity() {
      if (this.inputCommodityText === "") {
        this.offset = 0;
        this.findCommodity();
      }
    },
    // 加载跟多商品
    loadCommodityMore() {
      this.offset++;
      this.findCommodity();
    },
    // 确认选择的商品
    selectCommodityModalBtn(row) {
      if (row.stock == 0) {
        this.$Message.error("此商品库存为0");
        return;
      }
      let orderItem = this.orderData.orderItemList[this.itemIndex];
      if (orderItem.commodity_code != row.code) {
        for (var i = 0; i < this.orderData.orderItemList.length; i++) {
          if (row.code === this.orderData.orderItemList[i].commodity_code) {
            this.$Message.error("退货单中已有此商品");
            return;
          }
        }
      }
      orderItem.commodity_code = row.code;
      orderItem.bar_code = row.bar_code;
      orderItem.specifications = row.specifications;
      orderItem.price = row.price_in;
      orderItem.unit = "个";
      orderItem.name = row.name;
      this.selectCommodityModal = false;
      this.inputCommodityText = "";
      this.searchCommodityType = "name";
    },
    // 单位选择单价改变
    unitChange(row) {
      if (row.unit === "个") {
        if (row.specifications === 0) {
          row.price = keepTwoDecimalFull(0);
        } else {
          row.price = keepTwoDecimalFull(row.price / row.specifications);
        }
      } else {
        row.price = keepTwoDecimalFull(row.price * row.specifications);
      }
    },
    // 删除商品
    deleteSaleModal(index) {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定删除此商品？",
        onOk: () => {
          if (_this.orderData.orderItemList.length == 1) {
            _this.$Message.error("只剩一个条目，无法删除");
            return;
          }
          _this.orderData.orderItemList.splice(index, 1);
        },
        onCancel: () => {}
      });
    },

    // 保存确认
    saveSure() {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定保存此订单？",
        onOk: () => {
          _this.saveOrder();
        },
        onCancel: () => {}
      });
    },

    // 保存退货单
    saveOrder() {
      // 获取登录用户，作为下单员
      this.orderData.order_write_code = JSON.parse(
        sessionStorage.getItem("user")
      ).code;
      // 空判断
      if (
        this.orderData.customer_code == null ||
        this.orderData.customer_code === ""
      ) {
        this.$Message.error("客户未选择");
        return;
      }
      // 空判断
      if (
        this.orderData.order_write_code == null ||
        this.orderData.order_write_code === ""
      ) {
        this.$Message.error("下单员为空");
        return;
      }
      // 空判断
      if (this.orderData.state == null) {
        this.$Message.error("订单状态为空");
        return;
      }
      // 条目空判断
      if (
        this.orderData.orderItemList == null ||
        this.orderData.orderItemList.length === 0
      ) {
        this.$Message.error("订单没有商品");
        return;
      }
      for (var i = 0; i < this.orderData.orderItemList.length; i++) {
        if (
          this.orderData.orderItemList[i].commodity_code == null ||
          this.orderData.orderItemList[i].commodity_code == ""
        ) {
          this.$Message.error("有条目未选择商品");
          return;
        }
        if (
          this.orderData.orderItemList[i].num == null ||
          this.orderData.orderItemList[i].num == 0
        ) {
          this.$Message.error("有条目未填写商品数量");
          return;
        }
      }
      let _this = this;
      _this.$Spin.show();
      this.orderData.real_price = this.orderData.price;
      Http_Post(
        addOrder(this.orderData),
        function(datas) {
          if (datas.msg === 0) {
            _this.$Message.success("保存成功");
            _this.$Spin.hide();
            // 清空数据
            _this.orderData = {
              customer_code: "",
              order_write_code: "",
              num: 0,
              price: "",
              shop: "",
              state: 0,
              real_price: "0.00",
              orderItemList: [] // 条目列表
            }; // 提交的订单对象
            _this.addItem();
            EventBus.$emit("catBackOrder", {
              code: datas.data.code
            });
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },

    // 查询客户
    findCustomer() {
      let _this = this;
      _this.$Spin.show();
      Http_Post(
        findCustomer(
          _this.inputCustomerText,
          _this.searchCustomerType,
          _this.offset,
          _this.limit
        ),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isCustomerShowMoreBtn = false;
            } else {
              _this.isCustomerShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.customerList = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.customerList.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 查询商品
    findCommodity() {
      let _this = this;
      this.$Spin.show();
      Http_Post(
        findCommodity(
          _this.inputCommodityText,
          _this.searchCommodityType,
          _this.offset,
          _this.limit
        ),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isCommodityShowMoreBtn = false;
            } else {
              _this.isCommodityShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.commodityList = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.commodityList.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },

    // 添加新商品空条目
    addItem() {
      if (
        this.orderData.orderItemList.length != 0 &&
        this.orderData.orderItemList[this.orderData.orderItemList.length - 1]
          .commodity_code == ""
      ) {
        this.$message.error("上一个条目未选择商品，请选择后再添加新条目");
        return;
      }
      this.orderData.orderItemList.push({
        code: "",
        order_code: "",
        commodity_code: "",
        bar_code: "",
        num: 0,
        unit: "个",
        price: "0.00",
        specifications: 0,
        name: "",
        createTime: ""
      });
    }
  },

  created() {
    // 判断是否登录
    if (sessionStorage.getItem("user") == null) {
      this.$router.push({ name: "login" });
      return;
    }
    this.addItem();
  }
};
</script>
  
<style scoped>
</style>
